import React from 'react';
import './styles/app.css';
import Header from './components/Header';
import { Routes, BrowserRouter, Route } from 'react-router-dom';
import Main from './Route/Main';
import Curation from './Route/Curation';

const App = () => {

  return (
    <BrowserRouter>
      <Header />
      <Routes basename='/Nilda'>
        <Route  exact path="/Nilda" element={<Main />}/>
        <Route  exact path="/Nilda/curation" element={<Curation />}/>
      </Routes>
    </BrowserRouter>
  );
};

export default App;