export const xmlToJson = (xml) => {
  var arr = [];
  var items = xml.getElementsByTagName('item')

  for(var item of items) {
    var obj = {isOpen: false}
    var type = item.getElementsByTagName("ccmaName")[0].textContent;
    var name = item.getElementsByTagName("ccbaMnm1")[0].textContent;

    obj.lng = item.getElementsByTagName("longitude")[0].textContent;
    obj.lat = item.getElementsByTagName("latitude")[0].textContent;
    obj.content = <div style={{ color: "#000", width:'150px', padding:'5px' }}>[{type}] {name}</div>;

    arr.push(obj);
  }

  return arr;
}